import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import mu from '../../lib/musher-util';

@observer
class News extends Component {
    render() {
        const { news = {} } = this.props;
        return (
            <>
                <div class='w-100 mb-3'>
                    <div class='row'>
                        <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-0 position-relative'>
                            {/* {JSON.stringify(team, null, 4)} */}
                            <div class={`text-center border rounded-circle imageRounded`} style={news.images ? `background-image: url("${news.images[0].s3SmallLink}");` : ''}>
                                {!news.images && <i class='fas fa-paragraph text-muted mt-3' style='font-size: 40px;' />}
                            </div>
                            <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                                <a class='stretched-link' href={`/news/${news.id}`}>
                                    <h5 class='mb-1' style='line-height: 1.0em;'>{news.title}</h5>
                                </a>
                                <div>
                                    <small class='text-muted'>
                                        {util.isoDate(news.createdDate)}
                                    </small>
                                </div>
                                {news.ingress ? mu.maxLength(news.ingress, 50) : mu.maxLength(news.body, 50)}
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

export default News;
